import './App.css';
import './strings.js'
import Vol from './pages/vol';
import Ser from './pages/ser';
import API from './api';
import {Route, BrowserRouter as Router, Routes} from "react-router-dom"
import { useEffect, useState } from 'react';
import moment from 'moment'

function App() {

  var [data, setData] = useState({})
  var [dates, setDates] = useState([])

  useEffect(()=>{
    new API()
    .setFunc("getData").call()
    .then((data)=>{
      setData(data)
      var d = {}
      data.services.forEach(s => {
        d[s.date]=null
      });
      setDates(Object.keys(d).map(d=>{ return d }).sort())
    })
    .catch(()=>{})
  },[])

  return (
    <div className="App">
    <Router>
        <Routes>
          <Route path="/vol" element={<Vol data={data} dates={dates}/>}></Route>
          <Route path="/ser" element={<Ser data={data} dates={dates}/>}></Route>
        </Routes>
    </Router>
    </div>
  );
}

export default App;
